@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    font-family: 'Inter', sans-serif;
  }
}

@layer components {
  .btn-primary {
    @apply bg-gradient-to-r from-yellow-500 to-yellow-600 hover:from-yellow-600 hover:to-yellow-700 text-black font-bold py-3 px-6 rounded-lg transition-all duration-300 transform hover:scale-105;
  }
  
  .card {
    @apply bg-white/10 backdrop-blur-sm rounded-lg border border-white/20;
  }
  
  .input-field {
    @apply w-full p-3 bg-white/20 border border-purple-400 rounded-lg text-white placeholder-gray-300 focus:border-yellow-400 focus:outline-none focus:ring-2 focus:ring-yellow-400/50;
  }
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
}

::-webkit-scrollbar-thumb {
  background: rgba(251, 191, 36, 0.5);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(251,251, 251, 0.8);
}